import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    title: 'Home',
    icon: 'home',
    component: Home
  },
  {
    path: '/music',
    name: 'Music',
    title: 'Music',
    icon: 'music',
    component: () => import('../views/Music.vue')
  },
  {
    path: '/about',
    name: 'About',
    title: 'About',
    icon: 'cookie-bite',
    component: () => import('../views/About.vue')
  },
  {
    path: '/testpage',
    name: 'TestPage',
    title: 'Test Page',
    icon: 'dumpster',
    component: () => import('../views/TestPage.vue')
  },
  {
    path: '/vuehome',
    name: 'VueHome',
    title: 'Vue',
    icon: 'hand-spock',
    component: () => import('../views/VueHome.vue')
  }

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
