<template>
  <div class="home">
    <div class="flex">
      <h1 class="flex-auto mt-6 text-2xl font-bold light-mode:text-gray-900 leading-tight sm:mt-8 sm:text-4xl lg:text-3xl xl:text-4xl">Andy Around</h1>
      <div id="clock" class="flex-none text-2xl"></div>
    </div> 
    <p class="py-5">Not sure what to put here yet. maybe stuff from past trips</p>
    <div class="mx-auto flex flex-wrap">
      <div class="mt-6 w-full px-4 lg:w-1/2 xl:w-1/3" v-for="place in visitedPlaces" :key="place.id">
        <PlacesCard :place="place"/>
      </div>           
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import PlacesCard from '@/components/PlacesCard.vue'
export default {
  name: 'Home',
  components: {
    PlacesCard    
  },
  data() {
    return {
      visitedPlaces:[
        {
          id: 1,
          imageUrl: require('@/assets/img/Townsville.jpg'),
          imageAlt: 'Townsville',
          location: 'Townsville, AUS',
          locationInfo: 'This is a view of the City of Townsville in North Queensland, Australia. It was taken from the lookout at Mt. Stuart'
        },
        {
          id: 2,
          imageUrl: require('@/assets/img/crater_lake.jpg'),
          imageAlt: 'Crater Lake',
          location: 'Crater Lake, USA',
          locationInfo: 'Amazing blue of crater lake'
        },
        {
          id: 3,
          imageUrl: require('@/assets/img/craters_of_the_moon.jpg'),
          imageAlt: 'Craters of the Moon',
          location: 'Craters of the Moon, USA',
          locationInfo: 'Looking up one of the hills in Craters of the Moon national park'
        },
        {
          id: 4,
          imageUrl: require('@/assets/img/grand_tetons.jpg'),
          imageAlt: 'Grand Tetons',
          location: 'Grand Tetons, USA',
          locationInfo: 'Grand Tetons are quite an impressive site. This is a popular photo spot due to the barns and farmhouses'
        },
        {
          id: 5,
          imageUrl: require('@/assets/img/yellowstone.jpg'),
          imageAlt: 'Yellowstone',
          location: 'Yellowstone, USA',
          locationInfo: 'Just one small section of the massive geothermal area that is Yellowstone national park'
        },
        {
          id: 6,
          imageUrl: require('@/assets/img/methven.jpg'),
