        }
      ]
    }
  },
  mounted () {
    let self = this
    let o = document.getElementById('clock');
    // update clock every second
    this.interval = setInterval(()=>{self.updTime(o);}, 1000);
  },
  methods:{
    // function to pad a 0 if needed
    z: function(p) { 
      return p < 10 ? `0${p}` : p;
    },
    // function to update time. pass in element to update
    updTime: function(elm){
    let t = new Date();
    elm.innerHTML=`
        <text class="text-yellow-500">${this.z(t.getHours())}</text>:
        <text class="text-gray-500">${this.z(t.getMinutes())}</text>:
        <text class="text-pink-300">${this.z(t.getSeconds())}</text>`;
    }
  }
}
</script>
